import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { IonContent, IonSlides, IonSegment, ModalController } from '@ionic/angular';
import { BattleComponent } from './battle/battle.component';
import { BrModalComponent } from './br-modal/br-modal.component';

@Component({
  selector: 'app-battle-calculator',
  templateUrl: './battle-calculator.page.html',
  styleUrls: ['./battle-calculator.page.scss'],
})
export class BattleCalculatorPage implements OnInit, AfterViewInit {
  @ViewChild('ionContent', { static: true }) ionContent: IonContent;
  @ViewChild('ionSlides', { static: true }) ionSlides: IonSlides;
  @ViewChild('ionSegment', { static: true }) ionSegment: IonSegment;
  @ViewChild('battle', { static: true }) battleComponent: BattleComponent;

  modal: HTMLIonModalElement;
  isShowingModalInProgress: boolean;

  // onTop = true;
  activeSlideIndex =  parseInt(localStorage.getItem('activeSlideIndex')) || 0;
  // isContentScrollable = false;
  slideOpts = {
    allowTouchMove: false,
    autoHeight: true
  }

  oneOnOneData: any;
  battleData: any;
  codeData: any;

  constructor(
    private cdr: ChangeDetectorRef,
    private modalController: ModalController,
    private elRef: ElementRef
  ) { }

  ngOnInit() { }

  ngAfterViewInit() {
    // await this.checkIfScrollable();
  }

  onOneOnOneChange(data) {
    this.oneOnOneData = data;
    // this.cdr.detectChanges();
    // await this.checkIfScrollable();
  }

  onBattleChange(data) {
    this.battleData = data;
    // this.cdr.detectChanges();
    // await this.checkIfScrollable();
  }
  onCodeChange(data) {
    this.codeData = data;
    // this.cdr.detectChanges();
    // await this.checkIfScrollable();
  }

  scrollToBattleResult() {
    if (this.isOneOnOne) {
      this.ionContent.scrollToBottom(100)
    } else {
      const battleResultEl = this.elRef.nativeElement.querySelector('app-battle .optimal-result-block');
      battleResultEl && battleResultEl.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
    }
  }

  get isExecutingBattleMatches() {
    if (this.isOneOnOne) {
      return false;
    } else {
      return this.battleComponent.isExecutingBattleMatches;
    }
  }

  get isOneOnOne() {
    return this.activeSlideIndex === 1;
  }
  get isBattle() {
    return this.activeSlideIndex === 2;
  }
  get isCode() {
    return this.activeSlideIndex === 0;
  }

  get resultData() {
    if (this.isOneOnOne) {
      return this.oneOnOneData || {};
    } else if (this.isBattle) {
      return this.battleData || {};
    } else if (this.isCode){
      return this.codeData || {};
    } else {
      return {};
    }
  }

  // async checkIfScrollable() {
  //   const scrollableElement = await this.ionContent.getScrollElement();
  //   this.isContentScrollable = scrollableElement.scrollHeight > scrollableElement.clientHeight;
  //   this.cdr.detectChanges();
  // }


  // toggleScroll() {
  //   if (this.onTop) {
  //     this.ionContent.scrollToBottom(100);
  //     this.onTop = false;
  //   } else {
  //     this.ionContent.scrollToTop(100);
  //     this.onTop = true;
  //   }
  // }

  // async onScroll() {
  //   const scrollableElement = await this.ionContent.getScrollElement();
  //   if (scrollableElement.scrollTop > 0) {
  //     this.onTop = false;
  //   } else {
  //     this.onTop = true;
  //   }
  // }

  async onSlideChange() {
    this.activeSlideIndex = await this.ionSlides.getActiveIndex();
    localStorage.setItem('activeSlideIndex', this.activeSlideIndex + '');
  }

  // hasOneOnOneResult() {
  //   return this.oneOnOneData && this.oneOnOneData.battleResults;
  // }

  // hasBattleResult() {
  //   return this.battleData && this.battleData.battleResults;
  // }

  // showBattleResultButton() {
  //   if (this.activeSlideIndex === 0) {
  //     return this.hasOneOnOneResult();
  //   } else {
  //     return this.hasBattleResult();
  //   }
  // }

  async showBattleResultModal(event) {
    event.preventDefault();
    event.stopPropagation();

    if (this.isShowingModalInProgress || this.resultData.isInvalid) {
      return;
    }

    this.isShowingModalInProgress = true;

    const data = this.resultData;
    if (!this.isOneOnOne) {
      data.multi = true;
    }
    this.modal = await this.modalController.create({
      component: BrModalComponent,
      cssClass: 'br-modal',
      mode: 'ios',
      componentProps: data,
      swipeToClose: true,
      showBackdrop: true,
      backdropDismiss: true
    });
    await this.modal.present();
    this.isShowingModalInProgress = false;
  }
  

  onSegmentChange(event) {
    this.ionSlides.slideTo(event.detail.value);
  }
}
