import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { BattleResult, BattleResultStatistic } from '../../../models/battle-result.model';

@Component({
  selector: 'app-br-modal',
  templateUrl: './br-modal.component.html',
  styleUrls: ['./br-modal.component.scss'],
})
export class BrModalComponent implements OnInit {
  @Input() battleResults: BattleResult[];
  @Input() battleResultStatistics: BattleResultStatistic[];
  @Input() multi: boolean;
  @Input() showingOptimalResult: boolean;

  constructor(
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {}

  dismiss() {
    this.modalCtrl.dismiss();
  }

}
